function upArrow() {
	// Up arrow
	let upArrow = document.querySelector(".up-arrow");
	
	upArrow.addEventListener("click", () => {
		let scrollFromTop = window.scrollY,
			id = setInterval(function() {
				window.scrollBy(0, -40);
				scrollFromTop = window.scrollY;
				
				if (scrollFromTop === 0) {
					clearInterval(id);
				}
			}, 20);
	});
}

module.exports = upArrow;